import React, { Component, Fragment } from "react";
import Courses from "../../../components/Courses/Courses";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { connect } from "react-redux";
import {
  Auth,
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
  setUploadedQuestions,
  questionTypesStoreInterface,
  loadQuestionTypes,
  loadQuestionBank,
  setSelectedCourse,
  PrepareExamsStoreInterface,
  addExamQuestion,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  setSelectedPeriod,
  System,
  PushPopupAlert,
  loadExamsList,
  loadExamsListByPeriod,
  setSelectedExam,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { course } from "../../../interfaces/course";
import {
  QuestionInterface,
  uploadQuestionsResponse,
} from "../../../interfaces/question";
import Questions from "../../../components/Questions/Questions";
import Loading from "../../../components/Loading/Loading";
import { ApplicationPeriodGet } from "../../../interfaces/application";
import ApplicationPeriods from "../../../components/ApplicationPeriods/ApplicationPeriods";
import {
  ExamItemResponse,
  ExamItemResponseFull,
  QuestionNumberFormat,
} from "../../../interfaces/prepare_exam";
import { DATE } from "../../../utils/functions";
import { Link } from "react-router-dom";
import ExamDetails from "./ExamDetails";
import { EditExam } from "../EditExam/EditExam";
import { validateExamDate } from "../../../utils/question_bank";
import searchData from "../../../utils/search";

interface ExamsListProps {
  auth: Auth;
  selectedCourse: course;
  questionBank: QuestionBankStoreInterface;
  course: courseStoreInterface;
  questionTypes: questionTypesStoreInterface;
  prepareExam: PrepareExamsStoreInterface;
  system: System;
  setUploadingStatus: (status: boolean) => void;
  setSelectedCourse: (coure: course | null) => void;
  setUploadedQuestions: (questions: uploadQuestionsResponse | null) => void;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  loadQuestionBank: (
    course_id: string,
    callback: (loading: boolean) => void
  ) => void;
  addExamQuestion: (question: QuestionInterface) => void;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  loadExamsList: (callback: (loading: boolean) => void) => void;
  loadExamsListByPeriod: (
    period_id: string,
    callback: (loading: boolean) => void
  ) => void;
  loadCourses: (callback: (loading: boolean) => void) => void;
  setSelectedExam: (exam: ExamItemResponse) => void;
}
interface ExamsListState {
  loading: boolean;
  selectCourse: boolean;
  selectPeriod: boolean;
  addQuestionModal: boolean;
  formErr: {
    element: string;
    msg: string;
  } | null;
  questions_per_page: number;
  exam_date: string;
  start_time: string;
  duration: number;
  number_format: string;
  discription: string;
  termes: string;
  selectedExam: ExamItemResponse | null;
  examToEdit: ExamItemResponse | null;
  search: string;
}

class _ExamsList extends Component<ExamsListProps, ExamsListState> {
  constructor(props: ExamsListProps) {
    super(props);

    this.state = {
      loading: false,
      selectCourse: false,
      selectPeriod: false,
      addQuestionModal: false,
      formErr: {
        element: "",
        msg: "",
      },
      questions_per_page: 1,
      exam_date: "",
      start_time: "",
      duration: 1,
      number_format: QuestionNumberFormat.NUMBER,
      discription: "",
      termes: "",
      selectedExam: null,
      examToEdit: null,
      search: "",
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  addQuestionToPaper = (question: QuestionInterface) => {
    this.props.addExamQuestion(question);
  };
  removeToQuestionPaper = (question: QuestionInterface) => {
    if (
      window.confirm(
        "Are you sure do you want to remove question to question paper?"
      )
    ) {
      this.props.addExamQuestion(question);
    }
  };
  showPeriodFilter = (status: boolean) => {
    this.setState({ selectPeriod: status });
  };

  chooseCourse = (myCourse: course | null) => {
    let conf: boolean = false;
    if (this.props.prepareExam.questions.length !== 0) {
      conf = window.confirm(
        "Are you sure do you want to change course? You will loose your selected questions"
      );
    }
    if (conf === true) {
      for (let x of this.props.prepareExam.questions) {
        this.props.addExamQuestion(x);
      }
      this.props.setSelectedCourse(myCourse);
    }
    if (this.props.prepareExam.questions.length === 0) {
      this.props.setSelectedCourse(myCourse);
    }
    this.setState({ selectCourse: false });
  };

  examQuestionsIds = (questions: QuestionInterface[]): string[] => {
    let ids: string[] = [];
    for (let question of questions) {
      ids = [...ids, question.question_id];
    }
    return ids;
  };

  examsByPeriod = (Exam: ExamItemResponse[]): ExamItemResponseFull[] => {
    let temp = Exam.filter(
      (item) =>
        item.period === this.props.periods.selected_period?.session_plan_id
    );
    let newRes: ExamItemResponseFull[] = [];
    for (let item of temp) {
      newRes.push({
        questions: item.questions,
        exam_total_marks: item.exam_total_marks,
        status: item.status,
        period: item.period,
        course: item.course,
        course_id: item.course,
        course_code:
          this.props.course.courses === null
            ? ""
            : this.props.course.courses.find(
                (itm) => itm.course_id === item.course
              ) === undefined
            ? ""
            : this.props.course.courses.find(
                (itm) => itm.course_id === item.course
              )!.course_code,
        course_name:
          this.props.course.courses === null
            ? ""
            : this.props.course.courses.find(
                (itm) => itm.course_id === item.course
              ) === undefined
            ? ""
            : this.props.course.courses.find(
                (itm) => itm.course_id === item.course
              )!.course_name,
        exam_status: item.exam_status,
        termes: item.termes,
        questions_per_page: item.questions_per_page,
        exam_date: item.exam_date,
        start_time: item.start_time,
        end_time: item.end_time,
        duration: item.duration,
        number_format: item.number_format,
        discription: item.discription,
        createdAt: item.createdAt,
        updatedAt: item.updatedAt,
        id: item.id,
        rules: item.rules,
      });
    }
    return newRes;
  };

  componentDidMount = () => {
    if (
      this.props.questionTypes.questionTypes === null ||
      this.props.questionTypes.questionTypes.length === 0
    ) {
      this.props.loadQuestionTypes(this.setLoading);
    }
    if (
      this.props.periods.applicationPeriods === null ||
      this.props.periods.applicationPeriods.length === 0
    ) {
      this.props.loadApplicationPeriods(this.setLoading);
    }
    if (
      this.props.course.courses === null ||
      this.props.course.courses.length === 0
    ) {
      this.props.loadCourses(this.setLoading);
    }
    // if (this.props.course.selected_course === null) {
    //   this.setState({ selectCourse: true });
    // }
    if (
      this.props.questionBank.questions === null ||
      this.props.questionBank.questions.length === 0
    ) {
      if (this.props.course.selected_course !== null) {
        this.props.loadQuestionBank(
          this.props.course.selected_course.course_id,
          this.setLoading
        );
      }
    }
    // if (this.props.prepareExam.examsList === null || this.props.prepareExam.examsList.length === 0) {
    if (this.props.periods.selected_period !== null) {
      this.props.loadExamsListByPeriod(
        this.props.periods.selected_period.session_plan_id,
        this.setLoading
      );
    } else {
      this.props.auth.application_session &&
        this.props.loadExamsListByPeriod(
          this.props.auth.application_session.session_plan_id,
          this.setLoading
        );
    }
    // }
  };
  setSelectedExamItem = (exam: ExamItemResponse | null) => {
    this.setState({ selectedExam: exam });
  };
  setSelectedExamEdit = (exam: ExamItemResponse | null) => {
    this.setState({ examToEdit: exam });
    if (exam === null) {
      this.setState({ selectedExam: null });
    }
  };
  setUploadingStatus = (status: boolean) => {
    alert("Hello");
  };
  GetExamsByPeriod = (period: ApplicationPeriodGet) => {
    this.props.setSelectedPeriod(period);
    this.props.loadExamsListByPeriod(period.session_plan_id, this.setLoading);
  };
  render() {
    return (
      <Fragment>
        {this.state.examToEdit === null ? (
          <div>
            <div className="mt-3 rounded px-2 flex flex-col gap-3 bg-white py-2">
              <div className="flex flex-row items center justify-between">
                <div className="flex flex-row items-center">
                  {/* <RiFilePaperFill className="text-7xl text-blue-600" /> */}
                  <div className="flex flex-col px-2">
                    <span className="text-gray-600 text-2xl font-bold">
                      List of exams
                    </span>
                    <span className="text-blue-600 text-sm font-bold mt-1">
                      Period:{" "}
                      {this.props.periods.selected_period === null ? (
                        <span className="text-yellow-700 italic">
                          Not selected!
                        </span>
                      ) : (
                        `${
                          this.props.periods.selected_period.session_plan_name
                        } - ${
                          this.props.periods.selected_period.is_active === true
                            ? "Active"
                            : "Not valid!"
                        }`
                      )}
                    </span>
                    {/* <span className="text-blue-600 text-sm font-bold">
                    Course:{" "}
                    {this.props.course.selected_course === null ? (
                      <span className="text-yellow-700 italic">
                        Not selected!
                      </span>
                    ) : (
                      `${this.props.course.selected_course.course_code} - ${this.props.course.selected_course.course_name}`
                    )}
                  </span> */}
                  </div>
                </div>
                <div className="flex flex-row items-center gap-2">
                  <div
                    onClick={() => this.setState({ selectPeriod: true })}
                    className={`btn ${
                      this.props.periods.selected_period === null
                        ? "bg-yellow-50 hover:bg-yellow-100 border border-yellow-600 text-yellow-600"
                        : "bg-blue-50 hover:bg-blue-100 border border-blue-100 text-blue-600"
                    } h-max flex items-center justify-center h-10 cursor-pointer`}
                  >
                    {this.props.periods.selected_period === null
                      ? "Choose period"
                      : "Change period"}
                  </div>
                  <Link
                    to="prepare-exam"
                    className={`btn bg-blue-600 text-white h-max flex items-center justify-center h-10 cursor-pointer`}
                  >
                    Prepare exam
                  </Link>
                </div>
              </div>
            </div>
            {this.state.loading === true ? (
              <div className="px-4 py-6 mt-4 bg-white rounded">
                <div className="text-gray-600 mb-3 px-4">
                  Loading exams list, Please wait...
                </div>
                <Loading />
              </div>
            ) : (
              <div className="rounded bg-white px-3 py-3 mt-2 mb-28">
                <div className="w-full overflow-x-auto">
                  <table className="w-full">
                    <thead className="bg-blue-50 font-bold text-gray-800">
                      <tr>
                        <td className="py-2 pl-2 font-bold">#</td>
                        <td className="py-2">Course</td>
                        <td className="py-2">Exam date</td>
                        <td className="py-2">Start time</td>
                        <td className="py-2">End time</td>
                        <td className="py-2">Exam duration</td>
                        <td className="py-2">Exam</td>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td colSpan={7}>
                          <input
                            onChange={(
                              e: React.ChangeEvent<HTMLInputElement>
                            ) => this.setState({ search: e.target.value })}
                            type="text"
                            className="input-white border border-gray-500 my-2"
                            placeholder="Search"
                            value={this.state.search}
                          />
                        </td>
                      </tr>
                      {this.props.prepareExam.examsList === null ? (
                        <tr>
                          <td colSpan={7}>
                            <Loading />
                          </td>
                        </tr>
                      ) : searchData(
                          this.examsByPeriod(this.props.prepareExam.examsList),
                          this.state.search
                        ).length === 0 ? (
                        <tr>
                          <td colSpan={7}>
                            <div className="rounded bg-gray-100 px-3 py-3 flex items-center justify-center">
                              No exams found!
                            </div>
                          </td>
                        </tr>
                      ) : (
                        searchData(
                          this.examsByPeriod(this.props.prepareExam.examsList),
                          this.state.search
                        ).map((item, i) => (
                          <tr
                            title="Click to view details"
                            onClick={() =>
                              this.setState({ selectedExam: item })
                            }
                            key={i + 1}
                            className={`border-b border-gray-300 text-sm hover:bg-blue-50 cursor-pointer hover:border-blue-300`}
                          >
                            <td className="pl-2 px-2 font-bold">{i + 1}</td>
                            <td className="py-2">
                              <span className="font-bold">
                                {item.course_code}
                              </span>
                              -{item.course_name}
                            </td>
                            <td className="py-2">
                              {DATE(item.exam_date, "DD/MM/YYYY")}
                            </td>
                            <td className="py-2">{item.start_time}</td>
                            <td className="py-2">{item.end_time}</td>
                            <td className="py-2 font-bold text-blue-600">
                              {item.duration}mins
                            </td>
                            <td className="py-2">
                              {validateExamDate(
                                item.exam_date,
                                item.start_time,
                                item.end_time
                              )}
                            </td>
                          </tr>
                        ))
                      )}
                    </tbody>
                  </table>
                  {this.state.selectedExam !== null && (
                    <ExamDetails
                      course={this.props.course}
                      periods={this.props.periods}
                      exam={this.state.selectedExam}
                      setSelectedExam={this.setSelectedExamItem}
                      setSelectedExamEdit={this.setSelectedExamEdit}
                    />
                  )}
                </div>
              </div>
            )}
          </div>
        ) : (
          this.props.course.courses &&
          this.props.course.courses.find(
            (itm) =>
              this.state.examToEdit &&
              itm.course_id === this.state.examToEdit.course
          ) !== undefined && (
            <EditExam
              selectedCourse={
                this.props.course.courses.find(
                  (itm) =>
                    this.state.examToEdit &&
                    itm.course_id === this.state.examToEdit.course
                )!
              }
              selectedExam={this.state.examToEdit}
              setSelectedExam={this.setSelectedExamEdit}
              setUploadingStatus={this.setUploadingStatus}
              loadExamsList={this.props.loadExamsList}
              setLoadingProps={this.setLoading}
            />
          )
        )}
        {this.state.selectCourse === true && (
          <Modal
            title="Choose a course"
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ selectCourse: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.large}
            displayClose={true}
            padding={{ title: true }}
          >
            <Courses
              setSelectedCourse={this.chooseCourse}
              selected_course_id={
                this.props.course.selected_course &&
                this.props.course.selected_course.course_id
              }
            />
          </Modal>
        )}
        {this.state.addQuestionModal === true && (
          <Modal
            title={
              <div className="flex flex-row items-center gap-3">
                {this.props.questionBank.questions !== null &&
                  this.props.questionBank.questions.length > 0 && (
                    <Link
                      to={"/upload-questions"}
                      className="btn bg-blue-100 hover:bg-blue-200 text-blue-700 cursor-pointer"
                    >
                      Upload new
                    </Link>
                  )}
                <span>Add Exam Questions</span>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ addQuestionModal: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.large}
            displayClose={true}
            padding={{ title: true }}
          >
            {this.props.questionBank.questions === null ? (
              <Loading />
            ) : this.props.course.selected_course === null ? (
              <div>Please choose a course</div>
            ) : (
              <Questions
                selectedCourse={this.props.course.selected_course}
                callBack={this.addQuestionToPaper}
                questionsList={this.props.questionBank.questions}
                questionPaper={this.props.prepareExam.questions}
              />
            )}
          </Modal>
        )}
        {this.state.selectPeriod === true && (
          <Modal
            title="Choose a period"
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ selectPeriod: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.large}
            displayClose={true}
            padding={{ title: true }}
          >
            <div className="px-2">
              <ApplicationPeriods
                applicationPeriods={this.props.periods.applicationPeriods}
                setSelectedPeriod={this.GetExamsByPeriod}
                showPeriodFilter={this.showPeriodFilter}
              />
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  course,
  schoolLevels,
  questionBank,
  questionTypes,
  prepareExam,
  periods,
  system,
}: StoreState): {
  auth: Auth;
  course: courseStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionBank: QuestionBankStoreInterface;
  questionTypes: questionTypesStoreInterface;
  prepareExam: PrepareExamsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  system: System;
} => {
  return {
    auth: auth,
    course: course,
    schoolLevels: schoolLevels,
    questionBank: questionBank,
    questionTypes: questionTypes,
    prepareExam: prepareExam,
    periods: periods,
    system: system,
  };
};

export const ExamsList = connect(mapStateToProps, {
  loadCourses,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  setUploadedQuestions,
  loadQuestionTypes,
  loadQuestionBank,
  setSelectedCourse,
  addExamQuestion,
  loadApplicationPeriods,
  setSelectedPeriod,
  PushPopupAlert,
  loadExamsList,
  loadExamsListByPeriod,
  setSelectedExam,
})(_ExamsList);
